@import url(https://fonts.googleapis.com/css?family=Nunito&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Nunito', sans-serif;
}
body{
  background-color: #fff;
}
a {
  text-decoration: none;
}
img{
  pointer-events: none;
}
li{
  list-style: none;
}

::-webkit-scrollbar{
  width: 4px;
}

::-webkit-scrollbar-thumb{
  background-color: #5847bb;
  border-radius: 4px;
}

.themetoggle h1{
  color: #fff !important;
}
.themetoggle h2,.themetoggle h3,.themetoggle h4,.themetoggle h5{
  color: darkgray !important;
}

.themetoggle .footer{
  background-color: darkgray;
}

.themetoggle .footer h2,.themetoggle .footer h3,.themetoggle .footer h4{
  color: #000 !important;
}



.themetoggle .latestblog-card{
  border: 1.5px solid darkgray !important;
}

.themetoggle .topblog-card{
  border: 1.5px solid darkgray !important;
}

.themetoggle .ql-syntax{
  color: #000 !important;
}
.group{position:relative;margin:45px 0}.group .form-input{background:none;color:grey;font-size:18px;padding:10px 10px 10px 5px;display:block;width:100%;border:none;border-radius:0;border-bottom:1px solid grey;margin:25px 0;background-color:transparent}.group .form-input:focus{outline:none}.group .form-input:focus ~ .form-input-label{top:-10px;font-size:12px;color:#a9a9a9}.group input[type='password']{letter-spacing:0.3em}.group .form-input-label{color:grey;font-size:16px;font-weight:normal;position:absolute;pointer-events:none;left:5px;top:25px;-webkit-transition:300ms ease all;transition:300ms ease all}.group .form-input-label.shrink{top:-10px;font-size:12px;color:#a9a9a9}

.login-form-container{
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: #039be6;
}
.login-form-container .left{
  width: 40%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

.login-form-container .left img{
  width: 60%;
}
.login-form-container .left h1{
  color: #fff;
  font-size: 1.2rem;
}
.login-form-container .login-content{
  width: 20%;
  height: 80%;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 10px 20px rgba(159,162,177,.6);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.login-form-container .login-content h1{
  font-size: 1.8rem;
  color: #f39d00;
}
.login-form-container .login-content .login-form{
  height: 60%;
  width: 70%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.login-form-container .login-content .login-form button{
  background-color: #f39d00;
  border: 1.5px solid #f39d00;
  padding: 0.6rem 1rem;
  border-radius: 4px;
  font-size: 1rem;
  color: #fff;
  cursor: pointer;
}

.login-form-container .login-content .login-form button:hover{
  background-color: transparent;
  color: #f39d00;
}

.login-form-container .login-content .google-login{
  background-color: orangered;
  border: 1.5px solid orangered;
  padding: 0.6rem 1rem;
  border-radius: 4px;
  font-size: 1rem;
  color: #fff;
  cursor: pointer;
}

.login-form-container .login-content .google-login:hover{
  background-color: transparent;
  color: orangered;
}
.notfound{
    margin-top: 15vh;
    width: 100%;
    height: 40vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.notfound .top{
    width: 100%;
    height: 80%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.notfound .bottom{
    width: 90%;
    height: 15%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.notfound .top img{
    height: 100%;
}
